.workshop-card {
  color: #495464;
  min-height: 320px;
  width: 300px;
  border-radius: 5px;
  margin: 30px auto;
  background-color: #f4f4f2;
  box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
  -webkit-box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
  -moz-box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
  display: flex;
  flex-direction: column;
}

.workshop-card img {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  width: 300px;
  height: 170px;
  object-fit: cover;
} 

#workshop-card-host-img {
  height: 30px;
  width: 30px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 5px;
  margin-left: 3px;
}

#card-top {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
}

#card-bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 45px;
}

.card-host-container {
  display: flex;
}

.card-host-container span {
  font-weight: 600;
}

.icons {
  display: flex;

}

.icons i {
  padding: 0 4px;
}

#card-information {
  padding: 5px;
}

#card-title {
  color: #495464;
  font-size: 1.4rem;
  letter-spacing: 1px;
}
.edit-form {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: 20px auto;
  border: none;
  border-radius: 5px;
  background-color: #bbbfca;
  padding: 20px;
}

.edit-form label {
  color: #495464;
  letter-spacing: 1px;
  margin-bottom: 0px;
}

.edit-form input {
  background-color: #f4f4f2;
  border: none;
  border-radius: 3px;
  width: 200px;
  margin: 20px auto;
}

.edit-form textarea {
  background-color: #f4f4f2;
  border: none;
  border-radius: 3px;
  width: 250px;
  margin: 20px auto;
}

